<template>
  <div class="allstory">
      <div class="story-content">
        <div class="content" v-on:click="goIntro('xihu')">
          <img src="../../assets/images/xihu.png" alt="">
          <span></span>
          <p></p>
        </div>
        <div class="content" v-on:click="goIntro('binjiang')">
          <img src="../../assets/images/binjiang.png" alt="">
          <span></span>
          <p></p>
        </div>
        <div class="content" v-on:click="goIntro('xixi')">
          <img src="../../assets/images/xixi.png" alt="">
          <span></span>
          <p></p>
        </div>
        <div class="content" v-on:click="goIntro('dongxin')">
          <img src="../../assets/images/dongxin.png" alt="">
          <span></span>
          <p></p>
        </div>
        <div class="content" v-on:click="goIntro('nansong')">
          <img src="../../assets/images/nansong.png" alt="">
          <span>+</span>
          <p></p>
        </div>
        <div class="content" v-on:click="goIntro('nanshan')">
          <img src="../../assets/images/nanshan.png" alt="">
          <span></span>
          <p></p>
        </div>
        <div class="content" v-on:click="goIntro('hubin')">
          <img src="../../assets/images/hubin.png" alt="">
          <span></span>
          <p></p>
        </div>
      </div>
    </div>
</template>
<script>
export default {
    methods: {
        goIntro(item){
            this.$router.push('introStory?title='+item);
        }
    },
    mounted(){
        document.title = ''
    }
}
</script>
<style lang="less" scoped>
.allstory{
    margin-top: 20px;
    .story-content{
      width: 100%;
      box-sizing: border-box;
      padding: 0 12px 0 13px;
      & > .content{
          display: inline-block;
        width: 165px;
        height: 110px;
        margin: 4px;
        & > img{
          width: 100%;
          height: 100%;
        }
        & > span{
          font-size: 14px;
        }
        & > p{
          font-size: 12px;
          color: #666666;
        }
      }
    }
  }
</style>



