<template>
    <div class="article">
        <div class="top">
            <h1></h1>
            <h2> </h2>
            <p>09:00 - 16:00</p>
        </div>
        <p class="dingwei"><i class="iconfont icon-dingwei"></i>352</p>
        <div class="introText">
            4970
        </div>
        <div class="split">
            <div class="left"></div>
            <div class="content"></div>
            <div class="right"></div>
        </div>
        <div class="bright">
            <div>
                
            </div>
            <img src="../../assets/images/article/article1.png" alt="">
            <div>
                
            </div>
            <img src="../../assets/images/article/article2.png" alt="">
            <div></div>
            <img src="../../assets/images/article/article3.png" alt="">
        </div>
        <div class="split">
            <div class="left"></div>
            <div class="content"></div>
            <div class="right"></div>
        </div>
    </div>
</template>
<script>
export default {
    methods:{
       
    },
    mounted(){
        document.title = ''
    }
}
</script>
<style lang="less" scoped>
.article{
    background: #ffffff;
    .top{
        width: 100%;
        padding-top: 20px;
        & > h1{
            text-align: center;
            margin-bottom: 10px;
        }
        & > h2{
            text-align: center;
            margin-bottom: 10px;
        }
        & > p{
            text-align: center;
            margin-bottom: 10px;
        }
    }
    .dingwei{
        padding-left: 15px;
    }
    .introText{
        padding: 15px;
        font-size: 14px;
        line-height: 24px;
    }
    .split{
        width: 100%;
        display: flex;
        align-items: center;
        .left,.right{
            flex: 1;
            height: 18px;
            background: #f1f1f1;
        }
        .content{
            width: 100px;
            text-align: center;
            font-size: 18px;
        }
    }
    .bright{
        & > div{
            padding: 15px;
            font-size: 14px;
        }
        & > img{
            width: 100%;
            height: 233px;
        }
    }
  }
</style>



