<template>
  <div id="dingwei" class="dingwei">
    <dialogCity v-model="haveNoCity" type="fail" title="~" :content="showContent"></dialogCity>
    <div class="search">
      <input type="text" placeholder="" v-model.trim="info" @keyup="search($event)" @keydown.down="change()" @keydown.up.prevent="up()"><span class="verify" v-on:click="verify()">{{searchText}}</span>
      <i class="iconfont icon-search iconSearch"></i>
    </div>
    <ul class="searchData">
      <li v-for="(v, i) in myData" :class="{bg:i==index}" v-on:click="goSearch(v)">{{v}}</li>
    </ul>
    <!-- <h1 v-show="myData.length == 0"></h1> -->
    <div class="city">
      <div class="inland">
        <h2></h2>
        <ul class="inland-list">
          <li v-for="item in cityList.cityList1" v-on:click="backMain(item)">{{item}}</li>
        </ul>
        <h2></h2>
        <ul class="inland-list">
          <li v-for="item in cityList.cityList2" v-on:click="backMain(item)">{{item}}</li>
        </ul>
      </div>
      <!-- <footerBar router="dingwei"/> -->
    </div>
    <!-- <otherCity v-show="haveNoCity" :showMsg="showMsg"></otherCity> -->
  </div>
</template>
<script>
  import footerBar from '../components/footerBar.vue'
  import otherCity from '../components/otherCity.vue'
  import dialogCity from '../components/dialog.vue'

  export default {
    name: 'dingwei',
    components: {
      otherCity,
      footerBar,
      dialogCity
    },
    data () {
      return {
        searchText: '',
        cityList: {
          cityList1: ['', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', '', ''],
          cityList2: ['', '', '', '', '', '', '', '', '']
        },
        haveNoCity: false,
        showMsg: {showOtherCity: true},
        myData: [],   // 
        info: '',   // input
        index: -1,   //  
        showContent: ''
      }
    },
    methods: {
      backMain (item) {
        if (item === '') {
          this.$router.push('main')
        } else {
          this.haveNoCity = true
          this.showMsg.showOtherCity = true
        }
      },
      search (e) {
        let self = this;
        console.log('search')
        // 
        if (e.keyCode === 38 || e.keyCode === 40) {
          return
        }
        // 
        if (e.keyCode === 13) {
          window.open('https://www.baidu.com/s?wd=' + self.info)
          self.info = ''
        }
        // 
        self.$http.jsonp('https://sp0.baidu.com/5a1Fazu8AA54nxGko9WTAnF6hhy/su?', {params: {
          wd: self.info
        }, 
          jsonp: 'cb'    // jsonp 
        }).then(function (data) {
          console.log(data)
          console.log(JSON.parse(data.bodyText).s);
          self.myData = JSON.parse(data.bodyText).s;
//          this.myData = data.data.s  // 
        }, function () {
          console.log('')
        })
      },
      // 
      change () {
        console.log('change')
        this.index++
        this.info = this.myData[this.index]  // 
        if (this.index === this.myData.length) {
          this.index = -1  // -1
        }
      },
      up () {
        console.log('up')
        this.index--
        this.info = this.myData[this.index]
        if (this.index === -1) {
          this.index = this.myData.length - 1
        }
      },
      verify(){
        let self = this;
        if(self.myData.length > 0){
          window.open('https://www.baidu.com/s?wd=' + self.info)
          self.info = ''
        }else{
          self.$router.push('main');
        }
      },
      goSearch(item){
        window.open('https://www.baidu.com/s?wd=' + item)
        self.info = ''
      }
    },
    watch: {
      myData(val){
        if(val.length > 0){
          this.searchText = ''
        }else{
          this.searchText = ''
        }
      },
      info(val){
        if(val == ''){
          myData.length = 0;
        }
      }
    }
  }
</script>
<style scoped="scoped" lang="less">
  *{
    margin: 0;
    padding: 0;
  }
  .search{
    height: 46px;
    line-height: 46px;
    border-bottom: 1px solid #efefef;
    text-align: center;
    position: relative;
    padding-left: 10px;
    color: #999999;
    & > input{
      width: 280px;
      height: 28px;
      border: 1px solid #999999;
      border-radius: 20px;
      outline: none;
      padding-left: 40px;
      box-sizing: border-box;
      &::-webkit-input-placeholder{
        color: #999999;
      }
    }
    .iconSearch{
      position: absolute;
      left: 40px;
      top: 3px;
    }
    .verify{
      display: inline-block;
      width: 50px;
      height: 100%;
      margin-left: 5px;
    }

  }
  .searchData{
    position: absolute;
    left: 0;
    top: 37px;
    width: 100%;
    background: #ffffff;
    & > li{
      height: 40px;
      line-height: 40px;
      padding-left: 40px;
      border-bottom: 1px solid #eeeeee;
      &:hover{
        color: #EFC917;
        background: #eeeeee;
      }
    }
  }
  .city{
    .inland{
      /*padding-top: 25px;*/
      & > h2{
        font-size: 20px;
        font-weight: 500;
        text-align: center;
        margin-top: 25px;
      }
      .inland-list{
        box-sizing: border-box;
        width: 100%;
        padding: 0 10px;
        overflow-x: hidden;
        & > li{
          box-sizing: border-box;
          float: left;
          width: 100px;
          height: 28px;
          line-height: 26px;
          color: #666666;
          border: 1px solid #eee;
          border-radius: 15px;
          background-color: #ffffff;
          text-align: center;
          margin: 9px;
          &:active{
            background-color: #efd41e;
          }
        }
      }
    }
  }


</style>



