<template>
  <div class="allActivity">
        <van-swipe :autoplay="3000" class="swipeContent">
            <van-swipe-item class="change">
                <img src="../../assets/images/findeat/allactivity1.png" alt="" v-on:click="goActivity('guihun')">
            </van-swipe-item>
            <van-swipe-item>
                <img src="../../assets/images/findeat/activity11.png" alt="" v-on:click="goActivity('guihun')">
            </van-swipe-item>
            <van-swipe-item>
                <img src="../../assets/images/findeat/activity22.png" alt="" v-on:click="goActivity('guihun')">
            </van-swipe-item>
            <van-swipe-item>
                <img src="../../assets/images/findeat/activity33.png" alt="" v-on:click="goActivity('guihun')">
            </van-swipe-item>
        </van-swipe>
        <div class="activityItems">
            <div class="item item1" v-on:click="goActivity('guihun')">
                <img src="../../assets/images/findeat/allactivity2.png" alt="">
                <div class="itemText">
                    <p class="title"></p>
                    <div class="info">
                        <p class="place"></p>
                        <p class="place">1027-1128</p>
                    </div>
                </div>
                <div class="itemTitle">
                    
                </div>
            </div>
            <div class="item item2" v-on:click="goActivity('guihun')">
                <img src="../../assets/images/findeat/allactivity3.png" alt="">
                <div class="itemText">
                    <p class="title"></p>
                    <div class="info">
                        <p class="place"></p>
                        <p class="place">1027-1031</p>
                    </div>
                </div>
                <div class="itemTitle">
                    
                </div>
            </div>
            <div class="item item3" v-on:click="goActivity('guihun')">
                <img src="../../assets/images/findeat/allactivity4.png" alt="">
                <div class="itemText">
                    <p class="title"></p>
                    <div class="info">
                        <p class="place">501</p>
                        <p class="place">1031</p>
                    </div>
                </div>
                <div class="itemTitle">
                    
                </div>
            </div>
            <div class="item item4" v-on:click="goActivity('guihun')">
                <img src="../../assets/images/findeat/allactivity5.png" alt="">
                <div class="itemText">
                    <p class="title"></p>
                    <div class="info">
                        <p class="place"></p>
                        <p class="place">1104-1107</p>
                    </div>
                </div>
                <div class="itemTitle">
                    
                </div>
            </div>
            <div class="item item5" v-on:click="goActivity('guihun')">
                <img src="../../assets/images/findeat/allactivity6.png" alt="">
                <div class="itemText">
                    <p class="title">The Color Run </p>
                    <div class="info">
                        <p class="place"></p>
                        <p class="place">1106</p>
                    </div>
                </div>
                <div class="itemTitle">
                    
                </div>
            </div>
            <div class="item item6" v-on:click="goActivity('guihun')">
                <img src="../../assets/images/findeat/allactivity7.png" alt="">
                <div class="itemText">
                    <p class="title">x</p>
                    <div class="info">
                        <p class="place">Reading Tree Coffee</p>
                        <p class="place">1204-0105</p>
                    </div>
                </div>
                <div class="itemTitle">
                    
                </div>
            </div>
        </div>
  </div>
</template>
<script>
export default {
    data() {
        return {
            
        }
    },
    methods:{
        goActivity(item){
          switch(item){
            case 'all':
              this.$router.push('allActivity');
              break;
            case 'guihun':
              this.$router.push('activity');
              break;
            case 'article':
              this.$router.push('article');
          }
        }
    },
    mounted(){
        document.title = '';
    }
}
</script>
<style lang="less" scoped>
.allActivity{
    padding-top: 25px;
    .swipeContent{
        text-align: center;
        img{
            width: 285px;
            height: 125px;
        }
    }
    .activityItems{
        margin-top: 30px;
        padding: 0 15px;
        .item{
            position: relative;
            box-sizing: border-box;
            padding-left: 15px;
            margin-bottom: 20px;
            & > img{
                position: absolute;
                top: 15px;
                left: 0;
                width: 158px;
                height: 120px;
            }
            .itemText{
                width: 100%;
                height: 152px;
                border-radius: 10px;
                background: #f1f1f1;
                box-sizing: border-box;
                padding: 15px 10px 15px 165px;
                display: flex;
                flex-direction: column;
                // justify-content: center;
                .title{
                    flex: 1;
                    font-size: 14px;
                    display: flex;
                    align-items: center;
                }
                .info{
                    flex: 1;
                    font-size: 10px;
                    color: #888888;
                    display: flex;
                    flex-direction: column;
                    justify-content: center;
                }
            }
            .itemTitle{
                position: absolute;
                top: 15px;
                left: 10px;
                width: 50px;
                height: 25px;
                line-height: 25px;
                text-align: center;
                color: #efd41e;
                font-size: 10px;
            }
        }
    }
}
</style>



