    methods:{
        chooseTitle(){
            let self = this;
            switch(this.msg){
                case 'xihu':
                    self.article = 'xihu';
                    document.title = '';
                    console.log(this.msg);
                    break;
                case 'binjiang':
                    self.article = 'binjiang';
                    document.title = '';
                    break;
                case 'xixi':
                    self.article = 'xixi';
                    document.title = '';
                    break;
                case 'dongxin':
                    self.article = 'dongxin';
                    document.title = '';
                    break;
                case 'nansong':
                    self.article = 'nansong';
                    document.title = '+';
                    break;
                case 'nanshan':
                    self.article = 'nanshan';
                    document.title = '';
                    break;
                case 'hubin':
                    self.article = 'hubin';
                    document.title = '';
                    break;
                default:
                    break;
            }
        },
        playVideo(){
            document.getElementsByClassName('video')[0].play();
        }
    },
    mounted(){
        // document.title = this.msg;
        this.chooseTitle();
    }
}
