<style lang="less" scoped>
.introStory{
    width: 100vw;
    height: 100vh;
    overflow-x: hidden;
    overflow-y: scroll;
    background: #efd41e;
    box-sizing: border-box;
    padding: 15px;
    .introContent{
        background: #ffffff;
        border-radius: 20px;
        padding: 10px 10px 20px;
        & > h2{
            font-weight: 500;
            line-height: 30px;
        }
        .detailContent{
            .videoPlace{
                position: relative;
                width: 80%;
                height: 150px;
                overflow: hidden;
                background: #efd41e;
                border-radius: 10px;
                margin: 0 auto 15px;
                & > img{
                    width: 100%;
                    height: 100%;
                }
                & > i{
                    position: absolute;
                    width: 40px;
                    height: 40px;
                    top: 50%;
                    left: 50%;
                    margin: -20px 0 0 -20px;
                    font-size: 40px;
                    color: #efd41e;
                }
            }
            & > p{
                font-size: 14px;
                line-height: 20px;
                text-indent: 2em;
                margin-bottom: 15px;
            }
        }
    }
}
</style>



